%flex-center{
    display: flex;
    justify-content: center;
    align-items: center;
}

%flex-end{
    display: flex;
    justify-content: flex-end;
}

%flex-start{
    display: flex;
    justify-content: flex-start;
}

%flex-between{
    display: flex;
    justify-content: space-between;
}

%flex-around{
    display: flex;
    justify-content: space-around;
}

%width{
    width: 100%;
    height: 100%;
}

